html, body {height: 100%;}

// body {background-color: #F4F7FB;}

.placeholder {height: 50px;}
.page-wrapper {padding-top: 20px;}

.bg-gray {background-color: #f2f2f5;}
.bg-dark-gray {background-color: #b7b7b7;}
.bg-dark-red {background-color: #ee3452;}
.bg-light-blue {background-color: #bce8f1;}

.navbar-default {
  border-width: 0 0 1px;
  border-radius: 0;
}

.navbar-ehr {
  background-color: #FFF;
  .navbar-brand,
  .navbar-nav>li>a {
    // color: #ccc;
  }
}

.navbar-ehr .navbar-nav>.active>a,
.navbar-ehr .navbar-nav>.active>a:hover,
.navbar-ehr .navbar-nav>.active>a:focus {
  background-color: inherit;
  position: relative;
  color: $navbar-bg;
  &:before {
    position: absolute;
    display: block;
    content: ' ';
    width: 100%;
    height: 4px;
    left: 0;
    bottom: 0;
    background-color: $navbar-bg;
    z-index: 0;
  }
}

.navbar-brand span.strong {
  font-weight: bold;
  color: $primary-color;
}

.btn-primary {
  background-color: $primary-color;
  border-color: $primary-color;
  background-image: none; //linear-gradient(to bottom, #337ab7 0%, #265a88 100%)
  &.active,
  &:active,
  &:hover,
  &:focus {
    background-color: $primary-hover-color;
    border-color: $primary-hover-color;
  }
}

.navbar-kaoq {
  background-color: #ef6262;
  .navbar-nav>li {
    >a:hover {
      color: #fff;
    }
    a {
      color: #fafafa;
    }
  }
  .navbar-nav>.active>a,
  .navbar-default .navbar-nav>.active>a:hover,
  .navbar-default .navbar-nav>.active>a:focus{
    color: #fff;
    background-color: #ed5454;
  }
}

.default-toolbar {
  margin: 15px 10px;
  &.no-padding {
    margin-left: 0;
    margin-right: 0;
  }
}

.required .star {color: #f95b5b;}

.color-green {color: #cec;}
.color-red {color: #f95b5b;}
.hidden {display: none;}
.dropbox {border: 2px dashed #ccc; width: 500px; height: 200px;}
.dropbox.active {border-color: #2cc;}
.table.handsontable {background-color: #ccc;}

.login-panel {
  position: relative;
  width: 400px;
  margin-left: -200px;
  left: 50%;
  top: 50px;
}

#viewport .navbar {
  margin-bottom: 0;
}

#previewTable {
  height: calc(100vh - 64px);
}

.mod-nav {
  background-color: #FFF;
  padding: 10px 15px;
  border-bottom: 1px solid #ebebeb;
}

.mod-body {
  padding-top: 25px;
}
